/**
 * Velocity Theme Variables
 * Adapted from Obsidian Velocity theme by Floodlight
 * Modern, Apple-inspired design system
 */

// Animation timing
$anim-duration-fast: 150ms;
$anim-duration-superfast: 100ms;
$anim-motion-swing: cubic-bezier(0.4, 0.0, 0.2, 1);
$anim-motion-smooth: cubic-bezier(0.25, 0.1, 0.25, 1);

// Font weights (Velocity uses variable font weights)
$font-thin: 250;
$font-extralight: 275;
$font-light: 350;
$font-normal: 450;
$font-medium: 550;
$font-semibold: 650;
$font-bold: 750;
$font-extrabold: 850;
$font-black: 900;

// Heading weights
$h1-weight: 325;
$h2-weight: 575;
$h3-weight: 575;
$h4-weight: 650;
$h5-weight: 675;
$h6-weight: 700;

// Heading sizes
$h1-size: 2.125em;
$h2-size: 1.5625em;
$h3-size: 1.4125em;
$h4-size: 1.2125em;
$h5-size: 1.0875em;
$h6-size: 1em;

// Line heights
$line-height-normal: 1.6;
$line-height-tight: 1.5;

// Border radius
$radius-xs: 4px;
$radius-s: 6px;
$radius-sm: 8px;
$radius-m: 10px;
$radius-ml: 12px;
$radius-l: 16px;
$radius-xl: 18px;
$radius-xxl: 20px;

// Spacing
$spacing-xs: 8px;
$spacing-s: 12px;
$spacing-m: 16px;
$spacing-l: 24px;
$spacing-xl: 32px;

// Light theme colors (using standard RGB for better browser support)
// Base colors - subtle purple-tinted grays
$light-base-00: #fefefe;
$light-base-05: #fcfcfc;
$light-base-10: #f5f5f6;
$light-base-20: #ebebed;
$light-base-25: #e1e1e4;
$light-base-30: #d8d8db;
$light-base-35: #c9c9cd;
$light-base-40: #bfbfc4;
$light-base-50: #b5b5bb;
$light-base-55: #a5a5ad;
$light-base-60: #8c8c96;
$light-base-70: #737380;
$light-base-80: #6b6b78;
$light-base-90: #484854;
$light-base-100: #3a3a44;

// Accent color - blue
$light-accent: #0084ff;
$light-accent-h: 212;
$light-accent-s: 100%;
$light-accent-l: 48%;

// Semantic colors
$light-red: #f02a1c;
$light-orange: #ff8f00;
$light-yellow: #e9bc00;
$light-green: #09b41d;
$light-cyan: #04b0a7;
$light-blue: #0084ff;
$light-purple: #b24aec;
$light-pink: #e14693;

// Dark theme colors
$dark-base-00: #1a1a1e;
$dark-base-05: #1e1e22;
$dark-base-10: #242428;
$dark-base-20: #2c2c32;
$dark-base-25: #34343c;
$dark-base-30: #3c3c46;
$dark-base-35: #484854;
$dark-base-40: #54545e;
$dark-base-50: #606068;
$dark-base-55: #6c6c78;
$dark-base-60: #8c8c96;
$dark-base-70: #b5b5bb;
$dark-base-80: #c9c9cd;
$dark-base-90: #e1e1e4;
$dark-base-100: #f5f5f6;

// Dark accent
$dark-accent: #00b4ff;
$dark-accent-h: 200;
$dark-accent-s: 100%;
$dark-accent-l: 49%;

// Dark semantic colors
$dark-red: #fb484f;
$dark-orange: #ff9f0a;
$dark-yellow: #ffd60a;
$dark-green: #32d74b;
$dark-cyan: #64d2ff;
$dark-blue: #0a84ff;
$dark-purple: #bf5af2;
$dark-pink: #ff375f;

// Transparency values for overlays and shadows
$trans-faint-00: rgba(0, 0, 0, 0.03);
$trans-faint-05: rgba(0, 0, 0, 0.0275);
$trans-faint-10: rgba(0, 0, 0, 0.05);
$trans-faint-20: rgba(0, 0, 0, 0.06);
$trans-faint-25: rgba(0, 0, 0, 0.07);
$trans-faint-30: rgba(0, 0, 0, 0.092);
$trans-faint-40: rgba(0, 0, 0, 0.114);
$trans-faint-50: rgba(0, 0, 0, 0.145);
$trans-faint-60: rgba(0, 0, 0, 0.32);

$trans-strong-00: rgba(255, 255, 255, 0.95);
$trans-strong-10: rgba(255, 255, 255, 0.8);
$trans-strong-20: rgba(255, 255, 255, 0.75);
$trans-strong-30: rgba(255, 255, 255, 0.4);
$trans-strong-40: rgba(255, 255, 255, 0.1);

// Dark mode transparency
$dark-trans-faint-00: rgba(255, 255, 255, 0.03);
$dark-trans-faint-10: rgba(255, 255, 255, 0.05);
$dark-trans-faint-20: rgba(255, 255, 255, 0.06);
$dark-trans-faint-30: rgba(255, 255, 255, 0.092);
$dark-trans-faint-40: rgba(255, 255, 255, 0.114);

$dark-trans-strong-00: rgba(0, 0, 0, 0.95);
$dark-trans-strong-10: rgba(0, 0, 0, 0.8);
$dark-trans-strong-20: rgba(0, 0, 0, 0.75);
$dark-trans-strong-30: rgba(0, 0, 0, 0.4);

// Shadows (light theme)
$shadow-xs: 0 0 0 1px $trans-faint-20,
  0 3px 6px -2px $trans-faint-10;

$shadow-s: 0 0.125px 0 0.5px $trans-faint-30,
  inset 0 -0.125px 1px 0px $trans-faint-30,
  0 0.75px 2px 0.5px $trans-faint-20;

$shadow-m: 0 2.5px 4px 0px $trans-faint-10,
  0px 15px 30px $trans-faint-25;

$shadow-code: 0 0.125px 0px 0.375px $trans-faint-50;

$shadow-hover: 0 0.125px 0px 0.375px $trans-faint-60;

// CSS Custom Properties (for runtime theming)
:root {
  // Colors
  --color-base-00: #{$light-base-00};
  --color-base-05: #{$light-base-05};
  --color-base-10: #{$light-base-10};
  --color-base-20: #{$light-base-20};
  --color-base-30: #{$light-base-30};
  --color-base-40: #{$light-base-40};
  --color-base-50: #{$light-base-50};
  --color-base-60: #{$light-base-60};
  --color-base-70: #{$light-base-70};
  --color-base-80: #{$light-base-80};
  --color-base-90: #{$light-base-90};
  --color-base-100: #{$light-base-100};

  --color-accent: #{$light-accent};
  --color-accent-hover: #{darken($light-accent, 10%)};
  --color-accent-faint: #{rgba($light-accent, 0.15)};

  --text-normal: var(--color-base-90);
  --text-muted: var(--color-base-70);
  --text-faint: var(--color-base-50);

  --background-primary: var(--color-base-05);
  --background-secondary: var(--color-base-10);

  --border-color: #{$trans-faint-30};
  --border-color-hover: #{$trans-faint-40};

  // Code
  --code-background: #{$trans-faint-00};
  --code-border: #{$trans-faint-20};

  // Links
  --link-color: var(--color-accent);
  --link-color-hover: var(--color-base-100);

  // Shadows
  --shadow-xs: #{$shadow-xs};
  --shadow-s: #{$shadow-s};
  --shadow-m: #{$shadow-m};
  --shadow-code: #{$shadow-code};
  --shadow-hover: #{$shadow-hover};
}

// Dark mode (if you want to add it later)
@media (prefers-color-scheme: dark) {
  :root {
    --color-base-00: #{$dark-base-00};
    --color-base-05: #{$dark-base-05};
    --color-base-10: #{$dark-base-10};
    --color-base-20: #{$dark-base-20};
    --color-base-30: #{$dark-base-30};
    --color-base-40: #{$dark-base-40};
    --color-base-50: #{$dark-base-50};
    --color-base-60: #{$dark-base-60};
    --color-base-70: #{$dark-base-70};
    --color-base-80: #{$dark-base-80};
    --color-base-90: #{$dark-base-90};
    --color-base-100: #{$dark-base-100};

    --color-accent: #{$dark-accent};
    --color-accent-hover: #{lighten($dark-accent, 10%)};
    --color-accent-faint: #{rgba($dark-accent, 0.15)};

    --border-color: #{$dark-trans-faint-30};
    --border-color-hover: #{$dark-trans-faint-40};

    --code-background: #{$dark-trans-faint-00};
    --code-border: #{$dark-trans-faint-20};
  }
}